import react from 'react';

const SearchBox = ({searchChange}) => {
    return(
        <div className='pa2'>
            <input 
            className='pa3 ba b--green bg-lightest-blue mw6' 
            type='search' 
            placeholder='search robots' 
            onChange={searchChange}
            />
        </div>
    )
}

export default SearchBox;